import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { compose } from 'redux';

import { getStatus, setProfilePhoto, setUserProfileData, updateStatus, putUserPersonalData } from '../../redux/profileReducer';
import ProfilePage from './ProfilePage';


class ProfileApi extends React.Component {
  constructor(props) {
    super(props);
  }

  refreshProfile() {
    let userId = this.props.match.params.userId;
    if (!userId) {
      userId = this.props.userId;
    }
    if (!userId) { this.props.history.push('/login') }
    this.props.setUserProfileData(userId);
    this.props.getStatus(userId);
  }
  componentDidMount() {
    this.refreshProfile();
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props.match.params.userId !== prevProps.match.params.userId) {
      this.refreshProfile();
    }
  }


  render() {
    return (<ProfilePage {...this.props} isOwner={!this.props.match.params.userId} />)
  }
}
let mapStateToProps = (state) => {
  return {
    state: state.profile,
    profileInfo: state.profile.profileInfo,
    status: state.profile.status,
    userId: state.authentication.id,

  }
}

export default compose(withRouter,
  connect(mapStateToProps, { setUserProfileData, getStatus, updateStatus, setProfilePhoto, putUserPersonalData }))(ProfileApi)