import React from 'react';
import styles from './Profile.module.css';
import NewPostContainer from './MyPosts/NewPost/NewPostContainer'
import Post from './MyPosts/Post/Post'
import Profile from './Profileinfo/Profile';


function ProfilePage({ putUserPersonalData, setUserProfileData, isOwner, setProfilePhoto, ...props }) {
  let postsElem = props.state.posts.map(elem =>
    <Post message={elem.message} key={elem.id} likesCount={elem.likesCount} />
  );

  return (
    <div className={styles.block}>
      <div className={styles.profile_wrap}>
        <Profile profileInfo={props.profileInfo} putUserPersonalData={putUserPersonalData} setProfilePhoto={setProfilePhoto} status={props.status} isOwner={isOwner} updateStatus={props.updateStatus} />
      </div>
      <div><NewPostContainer /></div>
      <div className={styles.posts_wrap}>{postsElem}</div>
    </div>

  );
}

export default ProfilePage;