import React from 'react';


let ProfileData = ({ enableEditMode, isOwner, lookingForAJob, lookingForAJobDescription, fullName, contacts, ...props }) => {
    let Contact = ({ title, link }) => {
        return (link ? <><li>{title}: {link}</li></> : null)
    }
    return (
        <div >
            {isOwner && <button onClick={enableEditMode}>Edit Profile</button>}
            <p>{fullName}</p>
            <span>
                <p>lookingForAJob:{lookingForAJob ? "yes" : "no"}</p>
                {lookingForAJob ? <p>lookingForAJobDescription: {lookingForAJobDescription}</p> : null}
            </span>
            <span>My Contacts:
                <ul>
                    {Object.keys(contacts).map(e => <Contact key={e} title={e} link={contacts[e]} />)}
                </ul>
            </span>
        </div>
    )
}
export default ProfileData;