import React from 'react';
import { reduxForm, Field } from 'redux-form';
import { Input } from '../../common/Preloader/formsControlls/FormsControlls';


let ProfileDataForm = ({ disableEditMode, }) => {

    let DataForm = (props) => {

        return (
            <form onSubmit={props.handleSubmit}>
                <Field name='fullName' component={Input} type={'text'} placeholder='Your nickname' />
                <label htmlFor={'lookingForAJob'}>Looking for a job</label>
                <Field name='lookingForAJob' component={'input'} type={'checkbox'} /><br></br>
                <Field name='lookingForAJobDescription' component={'textarea'} placeholder='LookingForAJobDescription' />
                <Field name='aboutMe' component={'textarea'} placeholder='aboutMe' />


                <button >SaveChanges</button>
            </form>)
    }

    let ReduxDataForm = reduxForm({ form: 'UserDataForm' })(DataForm);


    return (
        <div>
            <ReduxDataForm onSubmit={disableEditMode} />
        </div>)
}


export default ProfileDataForm;