import React, { useState } from 'react';
import s from './Profile.module.css'
import defautUserPhoto from '../../../assets/images/defUser.jpeg';
import Preloader from '../../common/Preloader/Preloader';
import ProfileStatusWithHooks from './ProfileInfoStatus/ProfileInfoStatusWIthHooks';
import ProfileData from './ProfileData';
import ProfileDataForm from './ProfileDataForm';

function Profile({ putUserPersonalData, isOwner, setProfilePhoto, ...props }) {

    const [isEditMode, setIsEditMode] = useState(false)
    if (!props.profileInfo) {
        return (<Preloader />)
    }



    let onProfilePhotoSelected = (e) => {
        if (e.target.files.length) {
            setProfilePhoto(e.target.files[0])
        }
    }
    let ProfilePhoto = (props) => {

        return (<div className={s.profile_photo}>
            <img alt="profile_photo" className={s.mainPhoto} src={props.photos.large ? props.photos.large : defautUserPhoto} />
            {isOwner && <input type={"file"} onChange={onProfilePhotoSelected} />}
            <span>{props.aboutMe || null}</span>
        </div>)
    }
    let MemoizedProfilePhoto = React.memo(ProfilePhoto, (prev, next) => prev === next)

    let enableEditMode = () => {
        setIsEditMode(true);
    }
    let disableEditMode = (data) => {
        setIsEditMode(false);
        putUserPersonalData(data);
    }


    return (

        <div className={s.wrap}>
            <div className={s.profile_wrap}>
                <MemoizedProfilePhoto photos={props.profileInfo.photos} aboutMe={props.profileInfo.aboutMe} />
                {!isEditMode && <ProfileData {...props.profileInfo} isOwner={isOwner} enableEditMode={enableEditMode} />}
                {isEditMode && <ProfileDataForm disableEditMode={disableEditMode} />}

                <ProfileStatusWithHooks status={props.status} updateStatus={props.updateStatus} />
            </div>
        </div>
    );
}


export default Profile;